/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Roboto", sans-serif;
}
@media (min-width: 1024px) {
  .container {
    display: flex;
    flex-direction: row;
    max-width: 1440px;
    margin: 96px auto;
  }
}
.container .image {
  display: flex;
  justify-content: center;
}
.container .image img {
  width: 95%;
  height: auto;
  border-radius: 15px;
  padding: 8px 0;
}
@media (min-width: 1024px) {
  .container .image img {
    width: 505px;
    height: 330px;
    padding: 0;
    border-radius: 0;
    width: 100%;
  }
}
.container .content {
  max-width: 380px;
  max-height: 250px;
  padding: 8px;
  color: #121212;
}
@media (min-width: 1024px) {
  .container .content {
    margin-left: 95px;
  }
}
.container .content .title {
  font-size: 42px;
  font-weight: 700;
}
.container .content .text {
  margin: 24px 0 32px 0;
  line-height: 150%;
}

.btn {
  width: 128px;
  height: 48px;
  font-weight: 700;
  color: white;
  border-radius: 32px;
  background-color: #E4C1AE;
  border: none;
}
.btn:hover {
  cursor: pointer;
}

.resetButton {
  background-color: tomato;
  padding: 15px;
  margin: 15px 15px;
  opacity: 0;
}
.resetButton:hover {
  cursor: pointer;
}
.resetButton.active {
  opacity: 1;
}

.popup {
  background-color: white;
  position: fixed;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  width: 400px;
  max-width: 75%;
  display: flex;
  flex-direction: column;
  align-items: baseline;
  transition: 100ms ease-in-out;
  border-radius: 15px;
}
.popup.active {
  transform: translate(-50%, -50%) scale(1);
}
@media (min-width: 1024px) {
  .popup {
    width: 920px;
    height: 210px;
    border-radius: 0;
  }
}
.popup .popop-title {
  color: #121212;
  font-size: 42px;
  font-weight: 700;
  line-height: 120%;
  padding: 10px 12px;
}
@media (min-width: 1024px) {
  .popup .popop-title {
    padding: 56px 95px;
  }
}
.popup .close-button {
  cursor: pointer;
  border: none;
  outline: none;
  background-color: transparent;
  font-size: 35px;
  font-weight: bold;
  position: absolute;
  right: 5px;
  top: 5px;
}
@media (min-width: 1024px) {
  .popup .close-button {
    right: 46px;
    top: 48px;
  }
}
.popup .popup-body {
  padding: 10px 12px;
}
.popup .popup-body .strong {
  font-weight: 700;
}
@media (min-width: 1024px) {
  .popup .popup-body {
    padding: 0 95px;
  }
}
.black-transparent {
  position: fixed;
  opacity: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  pointer-events: none;
  transition: 100ms ease-in-out;
}
.black-transparent.active {
  pointer-events: all;
  opacity: 1;
}